<template id="">
  <div class="">
    <note v-for="(n,i) in melodie" :key=i :note=n @change="update"></note>
    <input type="button" value="play" @click="play">
    <input type="button" value="stop" @click="stop">
    <div class="alert">

    </div>
  </div>
</template>


<script type="text/javascript">

  import Tone from 'tone';
  var son = new Tone.Synth().toMaster();
  Tone.Transport.start();

  import note from './note.vue';
  export default{
    components:{note},
    data(){
      return {
        sequence: new Tone.Sequence((t,v)=>{
          console.log(v);
          console.log(t);
        }),
        melodie:[0,1,5,1,2,4,1,2]
      }
    },
    mounted(){
      this.update();
    },
    methods:{
      update(){
        this.sequence.removeAll();
        this.melodie.forEach((v,i)=>{
          this.sequence.add(i,v)
        })
        this.sequence.events = this.melodie;
      },
      stop(){
        this.sequence.stop();
      },
      play(){
        this.sequence.start();
      }
    }
  }
</script>

<style media="screen">

</style>



